.video {
  height: 100vh;
  position: relative;
}
.video video {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.video-overlay {
  position: absolute;
  inset: 0;
  background: #00000065;
}
.video-overlay-circle {
  width: 10rem;
  height: 10rem;
  border: 1px solid var(--color-golden);
  border-radius: 50%;
  cursor: pointer;
}
