.header {
  background-color: var(--color-black);
}
.header h1 {
  margin-top: 2rem;
  font-family: var(--font-base);
  color: var(--color-golden);
  letter-spacing: 0.2rem;
  text-transform: uppercase;
  line-height: 117px;
  font-size: 11rem;
}
.header p {
  margin: 2rem;
  margin-left: 0;
}
.header button {
  margin-left: 0;
}

.app-wrapper_img img {
  width: 80%;
}

@media screen and (max-width: 2000px) {
  .header h1 {
    line-height: 120px;
    font-size: 15rem;
  }
}
@media screen and (max-width: 1150px) {
  .header h1 {
    line-height: 120px;
    font-size: 12rem;
  }
}
@media screen and (max-width: 850px) {
  .header h1 {
    line-height: 80px;
    font-size: 9rem;
  }
}
@media screen and (max-width: 650px) {
  .header h1 {
    line-height: 70px;
    font-size: 7rem;
  }
}
@media screen and (max-width: 350px) {
  .header h1 {
    line-height: 60px;
    font-size: 6.5rem;
  }
}
