.aboutus {
  position: relative;
}

.aboutus-overlay {
  position: absolute;
  inset: 0;
}

.aboutus-overlay img {
  width: 39.5rem;
  height: 42rem;
  z-index: 0;
}
.aboutus-content {
  width: 100%;
  z-index: 2;
}

.aboutus-content-about {
  flex: 1;
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  flex-direction: column;

  text-align: right;
}

.aboutus-content-about-knife {
  margin: 2rem 4rem;
}

.aboutus-content-about-knife img {
  height: 91rem;
}
.aboutus-content-history {
  flex: 1;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;

  text-align: left;
}

.aboutus-content-about p,
.aboutus-content-history p {
  margin: 2rem 0;
  color: var(--color-grey);
}

@media screen and (max-width: 2000px) {
  .aboutus-content-about p,
  .aboutus-content-history p {
    margin: 4rem 0;
  }
  .aboutus-content-about-knife img {
    height: 110rem;
  }
}

@media screen and (max-width: 900px) {
  .aboutus-content {
    flex-direction: column;
  }

  .aboutus-content-about-knife {
    margin: 4rem 0;
  }
}

@media screen and (max-width: 650px) {
  .aboutus-overlay img {
    width: 80%;
    height: 32rem;
  }
}
