.gallery {
  display: flex;
  flex-direction: row;

  background: var(--color-black);
}
.gallery-content {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
  min-width: 50rem;
  padding-right: 2rem;
  margin-left: 0;
}

.gallery-content button {
  margin-top: 1rem;
  margin-left: 0;
}

.gallery-images {
  flex: 1;
  display: flex;
  flex-direction: row;
  max-width: 50%;
  position: relative;
}
.gallery-images-container {
  display: flex;
  flex-direction: row;
  width: max-content;
  overflow-x: scroll;
  -ms-overflow-style: none;
  scrollbar-width: none;
}

.gallery-images-container::-webkit-scrollbar {
  display: none;
}

.gallery-images-card {
  position: relative;
  min-width: 30.1rem;
  height: 44.7rem;
  margin-right: 2rem;
}

.gallery-images-card img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  opacity: 1;
  transition: 0.5s ease;
}
.gallery-images-card:hover img {
  opacity: 0.35;
}

.gallery-images-card:hover .gallery-image-icon {
  opacity: 1;
}

.gallery-image-icon {
  position: absolute;
  color: #fff;
  font-size: 2rem;
  opacity: 0.5;
  transition: 0.5s ease;
  cursor: pointer;
}

.gallery-images-arrows {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 1rem;
  position: absolute;
  bottom: 5%;
}

.gallery-arrows-icon {
  color: var(--color-golden);
  font-size: 2rem;
  cursor: pointer;
  background: var(--color-black);
  border-radius: 0.5rem;
}
.gallery-arrows-icon:hover {
  color: var(--color-white);
}

@media screen and (min-width: 2000px) {
  .gallery-content {
    min-width: 1000px;
    padding-right: 4rem;
  }

  .gallery-images-card {
    min-width: 40rem;
    height: 55rem;
  }
}
@media screen and (max-width: 1150px) {
  .gallery {
    flex-direction: column;
  }

  .gallery-images {
    margin: 5rem 0;
    max-width: 100%;
  }
}
@media screen and (max-width: 850px) {
  .gallery {
    padding: 4rem 0 4rem 4rem;
  }
}
@media screen and (max-width: 650px) {
  .gallery {
    padding: 4rem 0 4rem 2rem;
  }

  .gallery-content {
    min-width: 100%;
  }
  .gallery-images-card {
    min-width: 24rem;
    height: 32rem;
  }
}
