.laurel-awards {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: center;

  margin-top: 3rem;
}

.laurels-awards-card {
  flex: 1;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  min-width: 23rem;

  margin: 1rem;
}

.laurels-awards-card img {
  width: 5rem;
  height: 5rem;
  margin: 1rem 0.5rem 0 0;
}

.laurels-awards-card p:first-child {
  margin: 0 0 1rem 0;
}

.laurels-awards-card-content {
  display: flex;
  flex-direction: column;
  margin-left: 1rem;
}

@media screen and (min-width: 1900px) {
  .laurels-awards-card {
    min-width: 39rem;
  }
}
@media screen and (max-width: 450px) {
  .laurels-awards-card {
    min-width: 100%;
    margin: 1rem 0;
  }
}
